





























import axios, { AxiosError } from 'axios';
import { Component, Vue } from 'vue-property-decorator';
import { Player } from '../models/Player';
import { Game } from '../models/Game';


@Component
export default class RoomComponent extends Vue {
  public rooms: Array<Game> = [];

  public gameId = '';

  public polling = -1;

  public appHost = process.env.LOCAL_ADDRESS;

  create() {
    const key = Math.floor(Math.random() * 100);
    axios.get(`http://localhost:8080/newGame?name=${`Room-${key}`}`).then((res) => {
      this.gameId = res.data;
      this.rooms.push({
        gameName: `Room-${key}`,
        players: [{} as Player, {} as Player, {} as Player, {} as Player],
      });
    }).catch((error: AxiosError) => {
      this.handleError(error);
    });
  }

  joinRoom(room: Game, index: number, player: Player, playerIndex: number) {
    axios.get(`http://localhost:8080/addPlayer?name=${player.tempName}&gameId=${room.gameId}`).then((res) => {
      const state = room;
      if (state && state.players) {
        state.players[playerIndex] = res.data.player;
        state.me = res.data.player;
        Vue.set(this.rooms, index, state);
      }
    }).catch((error: AxiosError) => {
      this.handleError(error);
    });
  }

  start(gameId: string) {
    if (this.rooms && this.rooms[0] && this.rooms[0].players && this.rooms[0].players[0]) {
      const playerId = this.rooms[0]?.players[0]?.id;
      axios.get(`http://localhost:8080/startGame?playerId=${playerId}&gameId=${gameId}`).then((res) => {
        this.$router.push({ name: 'Game', params: { id: playerId || '', gameId } });
      }).catch((error: AxiosError) => {
        this.handleError(error);
      });
    }
  }

  refreshState() {
    const playerId = this.rooms[0]?.me?.id;
    if (playerId) {
      axios.get(`http://localhost:8080/getPlayersState?playerId=${playerId}&gameId=${this.gameId}`).then((res) => {
        const { state } = res.data.state;
        state.players[res.data.player.position] = res.data.player;
        res.data.opponents.forEach((player: Player) => {
          if (player.position) {
            state.players[player.position] = player;
          }
        });
        this.rooms = [state];
      }).catch((error: AxiosError) => {
        this.handleError(error);
      });
    }
  }

  created() {
    this.getAllGames();
    this.polling = setInterval(() => {
      this.getAllGames();
    }, 5000);
  }

  getAllGames() {
    axios.get('http://localhost:8080/getAllGames').then((res) => {
      const { data } = res;
      data.forEach((game: Game) => {
        if (game.players) {
          const arr = [{} as Player, {} as Player, {} as Player, {} as Player];
          game.players.forEach((player) => {
            if (player.position) {
              arr[player.position - 1] = player;
            }
          });
          game.players = arr;
        }
      });
      this.rooms = data;
    }).catch((error: AxiosError) => {
      this.handleError(error);
    });
  }

  beforeDestroy() {
    clearInterval(this.polling);
    this.polling = -1;
  }

  handleError(error: AxiosError) {
    if (error.response) {
      alert(error.response.data.message);
    }
  }
}
